import { collection, getDocs } from "firebase/firestore";
import React from "react";
import { useLocation } from "react-router-dom";
import { useState, useEffect } from "react";
import db from "./firebase.js";
import { motion } from "framer-motion";
import Spinner from "./Spinner";
import ProjectTile from "./ProjectTile";
import ImageGrid from "./ImageGrid";
import Modal from "./Modal.jsx";

function Projects(props) {
  const location = useLocation();
  const [projects, setProjects] = useState([]);
  const [spinner, setSpinner] = useState(true);

  // const [selected, setSelected] = useState("architecture");
  const tabs = ["Architecture", "Interior", "Landscapes"];

  const [selected, setSelected] = useState(() => {
    if (location.state) {
      return location.state.tab;
    }

    return "Architecture";
  });
  const [selectedImg, setSelectedImg] = useState(null);

  // const toggleTab = (string) => {
  //   setToggleState(string);
  // };

  const collectionRef = collection(db, "projects");

  useEffect(() => {
    getDocs(collectionRef)
      .then((snapshot) => {
        let projects = [];
        snapshot.docs.forEach((doc) => {
          projects.push({ ...doc.data(), id: doc.id });
        });
        setProjects(projects);
      })
      .catch((err) => {
        console.log(err);
      });
    // eslint-disable-next-line
  }, []);

  //Interior array building
  let imgUrl = [];
  let interiorProjects = [];

  interiorProjects = projects.filter((project) => {
    return project.category.includes("interior");
  });

  interiorProjects.forEach((project) => {
    imgUrl.push(...project.interior);
  });

  useEffect(() => {
    if (projects.length > 0) {
      setSpinner(false);
    }
  }, [projects]);

  return (
    <>
      <div className="projects-header">
        <h2>
          OUR <span>PROJECTS</span>
        </h2>
      </div>
      <div className="projects-container">
        <div className="tabs-container">
          {tabs.map((item) => (
            <div
              className={selected === item ? "tab active-tab" : "tab"}
              key={item}
              onClick={() => setSelected(item)}
              tabIndex={0}
            >
              <span>{item}</span>

              {selected === item ? (
                <motion.div className="underline" layoutId="underline" />
              ) : null}
            </div>
          ))}
        </div>
        <div className="content-container">
          {spinner ? (
            <div className="projects-spinner-cont">
              <Spinner />
            </div>
          ) : (
            <motion.div
              className={
                selected === "Architecture"
                  ? "content active-content"
                  : "content"
              }
              initial={{ y: 10, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              transition={{ bounce: 0 }}
            >
              {projects &&
                projects
                  .filter((project) => {
                    return project.category.includes("architecture");
                  })
                  .map((project) => (
                    <ProjectTile key={project.id} project={project} />
                  ))}
              {projects &&
                projects
                  .filter((project) => {
                    return project.category.includes("architecture");
                  })
                  .map((project) => (
                    <ProjectTile key={project.id} project={project} />
                  ))}
            </motion.div>
          )}
          <motion.div
            initial={{ y: 10, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ bounce: 0 }}
            className={
              selected === "Interior" ? "content active-content" : "content"
            }
          >
            {projects && (
              <ImageGrid imgUrl={imgUrl} setSelectedImg={setSelectedImg} />
            )}
            {selectedImg && (
              <Modal
                selectedImg={selectedImg}
                setSelectedImg={setSelectedImg}
              />
            )}
          </motion.div>
          <motion.div
            initial={{ y: 10, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ bounce: 0 }}
            className={
              selected === "Landscapes" ? "content active-content" : "content"
            }
          >
            {projects &&
              projects
                .filter((project) => {
                  return project.category.includes("landscapes");
                })
                .map((project) => (
                  <ProjectTile key={project.id} project={project} />
                ))}
          </motion.div>
        </div>
      </div>
    </>
  );
}

export default Projects;
