import React from "react";
import { NavLink } from "react-router-dom";
import { useState } from "react";
import MobileNav from "./MobileNav";
import { ReactSVG } from "react-svg";
import Logo from "./svg/Logo.svg";
import { motion } from "framer-motion";

function Header({ selected, setSelected }) {
  const [isOpen, setIsOpen] = useState(false);

  function handleClick() {
    setIsOpen(!isOpen);
  }

  const path01 = {
    opened: { d: "M3.06061 2.99999L21.0606 21" },
    closed: { d: "M0 9.5L24 9.5" },
  };

  const path02 = {
    opened: { d: "M3.00006 21.0607L21 3.06064" },
    closed: { d: "M0 14.5L24 14.5" },
  };

  return (
    <>
      <motion.div
        className="header-wrapper"
        initial={{ y: "-105%" }}
        animate={{ y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <div className="header-container">
          <NavLink
            onClick={() => {
              setSelected("/");
            }}
            className="nav-links"
            to="/"
          >
            <div className="logo">
              <ReactSVG className="" src={Logo} />
              <p>
                GREEN<span>SPACES</span>
              </p>
            </div>
          </NavLink>

          <nav className="nav-bar">
            <NavLink
              onClick={() => {
                setSelected("/");
              }}
              className={
                selected === "/" ? "nav-links active-nav-links" : "nav-links"
              }
              to="/"
            >
              Home
            </NavLink>
            <NavLink
              onClick={() => {
                setSelected("/about");
              }}
              className={
                selected === "/about"
                  ? "nav-links active-nav-links"
                  : "nav-links"
              }
              to="about"
            >
              About
            </NavLink>
            <NavLink
              onClick={() => {
                setSelected("/projects");
              }}
              className={
                selected === "/projects"
                  ? "nav-links active-nav-links"
                  : "nav-links"
              }
              to="projects"
            >
              Projects
            </NavLink>
            <NavLink
              onClick={() => {
                setSelected("/contact");
              }}
              className={
                selected === "/contact"
                  ? "nav-links active-nav-links"
                  : "nav-links"
              }
              to="contact"
            >
              Contact Us
            </NavLink>
          </nav>

          <button onClick={handleClick} className="hamburger">
            <svg width="24" height="24" viewBox="0 0 24 24">
              <motion.path
                stroke="#000"
                animate={isOpen ? "opened" : "closed"}
                variants={path01}
              />
              <motion.path
                stroke="#000"
                animate={isOpen ? "opened" : "closed"}
                variants={path02}
              />
            </svg>
          </button>
          <MobileNav
            handleClick={handleClick}
            setIsOpen={setIsOpen}
            isOpen={isOpen}
            selected={selected}
            setSelected={setSelected}
          />
        </div>
      </motion.div>
    </>
  );
}

export default Header;
