import React from "react";
import { motion } from "framer-motion";
import { NavLink } from "react-router-dom";

function MobileNav({ isOpen, setIsOpen, handleClick }) {
  const menuVariants = {
    opened: {
      x: 0,
      transition: {
        type: "spring",
        bounce: 0,
        duration: 0.2,
        when: "beforeChildren",
        staggerChildren: 0.04,
      },
    },
    closed: {
      x: "100vw",
    },
  };

  const itemVariant = {
    opened: { opacity: 1, y: 0 },
    closed: { opacity: 0, y: "2rem" },
    transition: { duration: 0.1, delay: 3 },
  };

  return (
    <>
      {isOpen && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          onClick={handleClick}
          className="backdrop2"
        ></motion.div>
      )}
      {/* <AnimatePresence> */}
      {
        /*isOpen &&*/ <motion.div
          className="mnav-cont"
          initial={false}
          variants={menuVariants}
          animate={isOpen ? "opened" : "closed"}
          // exit={{ x: "100vw" }}
        >
          {/* <motion.nav className="mnav-bar"> */}
          <NavLink
            className="mnav-links"
            onClick={() => {
              handleClick();
            }}
            to="/"
          >
            <motion.div variants={itemVariant}> Home</motion.div>
          </NavLink>
          <NavLink
            className="mnav-links"
            onClick={() => {
              handleClick();
            }}
            to="about"
          >
            <motion.div variants={itemVariant}> About</motion.div>
          </NavLink>
          <NavLink
            className="mnav-links"
            onClick={() => {
              handleClick();
            }}
            to="projects"
          >
            <motion.div variants={itemVariant}> Projects</motion.div>
          </NavLink>
          <NavLink
            className="mnav-links"
            onClick={() => {
              handleClick();
            }}
            to="contact"
          >
            <motion.div variants={itemVariant}> Contact</motion.div>
          </NavLink>
        </motion.div>
      }
      {/* </AnimatePresence> */}
    </>
  );
}

export default MobileNav;
