import React from "react";
import { useState, useEffect } from "react";
import db from "./firebase.js";
import { getDoc, doc } from "firebase/firestore";
import BtnSlider from "./BtnSlider";
import { motion } from "framer-motion";
import Spinner from "./Spinner";

function Carousel() {
  const [carouselData, setCarouselData] = useState({});
  const [spinner, setSpinner] = useState(true);

  const docRef = doc(db, "homecarousel", "1");

  useEffect(() => {
    getDoc(docRef)
      .then((doc) => {
        setCarouselData(doc.data());
      })
      .catch((err) => {
        console.log(err);
      });
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (Object.keys(carouselData).length > 0) {
      setSpinner(false);
    }
  }, [carouselData]);

  const [slideIndex, setSlideIndex] = useState(1);

  const nextSlide = () => {
    if (slideIndex !== carouselData?.images.length) {
      setSlideIndex(slideIndex + 1);
    } else if (slideIndex === carouselData?.images.length) {
      setSlideIndex(1);
    }
  };

  const prevSlide = () => {
    if (slideIndex !== 1) {
      setSlideIndex(slideIndex - 1);
    } else if (slideIndex === 1) {
      setSlideIndex(carouselData?.images.length);
    }
  };

  const moveDot = (index) => {
    setSlideIndex(index);
  };

  return (
    <>
      {spinner ? (
        <div className="carousel-spinner-cont">
          <Spinner />
        </div>
      ) : (
        <motion.div
          className="container-slider"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{
            duration: 0.5,
          }}
        >
          {carouselData.images &&
            carouselData.images.map((obj, index) => {
              return (
                <div
                  key={obj.id}
                  className={
                    slideIndex === index + 1 ? "slide active-anim" : "slide"
                  }
                >
                  <img alt="video" src={obj} />
                </div>
              );
            })}
          <BtnSlider moveSlide={nextSlide} direction={"next"} />
          <BtnSlider moveSlide={prevSlide} direction={"prev"} />

          <div className="container-dots">
            {Array.from({
              length: carouselData.images && carouselData?.images.length,
            }).map((item, index) => (
              <div
                onClick={() => moveDot(index + 1)}
                className={slideIndex === index + 1 ? "dot active" : "dot"}
              ></div>
            ))}
          </div>
        </motion.div>
      )}
    </>
  );
}

export default Carousel;
