import React from "react";
import { ReactSVG } from "react-svg";
import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import Carousel from "./Carousel";
import homeilus from "./svg/home7.svg";
import mobileilus from "./svg/mobile.svg";
import architecture from "./svg/architecture.svg";
import interior from "./svg/interior.svg";
import landscapes from "./svg/landscapes.svg";
import { Link } from "react-router-dom";

function Home({ selected, setSelected }) {
  const [innerWidth, setInnerWidth] = useState(window.innerWidth);

  useEffect(() => {
    window.addEventListener("resize", () => {
      setInnerWidth(window.innerWidth);
    });
    return () =>
      window.removeEventListener("resize", () => {
        setInnerWidth(window.innerWidth);
      });
  }, [innerWidth]);

  const cardCont = {
    opened: {
      opacity: 1,

      transition: {
        type: "spring",
        bounce: 0,
        duration: 0.2,
        when: "beforeChildren",
        staggerChildren: 0.2,
      },
    },
    closed: {
      opacity: 0,
    },
  };

  const card = {
    opened: { y: 0, opacity: 1 },
    closed: { y: 10, opacity: 0 },
    transition: { bounce: 0 },
  };

  return (
    <>
      <div className="firstpage-wrapper">
        <motion.div
          className="firstpage-left-wrapper"
          initial={{ y: 10, opacity: 0 }}
          whileInView={{ y: 0, opacity: 1 }}
          viewport={{ once: true }}
          transition={{
            duration: 0.5,

            // delay: 0.2,
          }}
        >
          <h1>We Believe in </h1>
          <h1>Great Architecture</h1>
          <p>
            Lorem ipsum dolor sit amet, ipsum dolor sit amet consectetur
            adipisicing elit. Sapiente excepibero odio quia consectetur
            adipisicing elit. Nam eos necessita tibusia temporibus minus!
          </p>
          <a href="#thirdpage-container">
            <button className="button-26">Learn more</button>
          </a>
        </motion.div>

        {innerWidth < 785 ? (
          <ReactSVG className="mobileillus" src={mobileilus} />
        ) : (
          <ReactSVG className="homeillus2" src={homeilus} />
        )}
      </div>
      <div className="secondpage-wrapper">
        <div className="secondpage-wrapper-left">
          <Carousel />
        </div>
        <motion.div
          className="secondpage-wrapper-right"
          initial={{ y: 10, opacity: 0 }}
          whileInView={{ y: 0, opacity: 1 }}
          viewport={{ once: true }}
          transition={{
            duration: 0.5,
          }}
        >
          <h2>We are The Leader </h2>
          <h2>in The Architectural</h2>
          <p>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Non, quae
            quia. Sapiente excepibero odio quia similique culpa quisquam nisi
            ratione rem. Nam, eveniet.
          </p>
          <div className="grid-container">
            <div className="grid-items figures">850</div>
            <div className="grid-items figures">24</div>
            <div className="grid-items figures">15</div>
            <div className="grid-items achievement">Property Build</div>
            <div className="grid-items achievement">Awards Gained</div>
            <div className="grid-items achievement">Years Experience</div>
          </div>
        </motion.div>
      </div>
      <div id="thirdpage-container" className="thirdpage-container">
        <motion.h3
          id="services"
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          viewport={{ once: true }}
          transition={{ duration: 0.5 }}
        >
          Our <span> Services</span>
        </motion.h3>
        <motion.div
          className="thirdpage-wrapper-right"
          initial="closed"
          whileInView="opened"
          viewport={{ once: true }}
          variants={cardCont}
        >
          <motion.div
            className="project-card"
            variants={card}
            transition={{ bounce: 0 }}
          >
            <div className="card-icon">
              <ReactSVG className="icon" src={architecture} />
            </div>
            <h4>Architecture</h4>
            <p>
              Lorem ipsum dolor staircase elit. Rerum illum iure obcaecribus
              omnis. Corporis ipsoptio.
            </p>
            <Link
              onClick={() => {
                setSelected("/projects");
              }}
              to="projects"
              state={{ tab: "Architecture" }}
              className="projectcard-btn"
            >
              View
            </Link>
          </motion.div>
          <motion.div
            className="project-card"
            variants={card}
            transition={{ bounce: 0 }}
          >
            <div className="card-icon">
              <ReactSVG className="icon icon-i" src={interior} />
            </div>
            <h4>Interior Design</h4>
            <p>
              Lorem ipsum dolor staircase elit. Rerum illum iure obcaecribus
              omnis. Corporis ipsoptio.
            </p>
            <Link
              onClick={() => {
                setSelected("/projects");
              }}
              to="projects"
              state={{ tab: "Interior" }}
              className="projectcard-btn"
            >
              View
            </Link>
          </motion.div>
          <motion.div
            className="project-card"
            variants={card}
            transition={{ bounce: 0 }}
          >
            <div className="card-icon">
              <ReactSVG className="icon icon-l" src={landscapes} />
            </div>
            <h4>Landscapes</h4>
            <p>
              Lorem ipsum dolor staircase elit. Rerum illum iure obcaecribus
              omnis. Corporis ipsoptio.
            </p>
            <Link
              onClick={() => {
                setSelected("/projects");
              }}
              to="projects"
              state={{ tab: "Landscapes" }}
              className="projectcard-btn"
            >
              View
            </Link>
          </motion.div>
        </motion.div>
      </div>
    </>
  );
}

export default Home;
