import React from "react";
import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { motion } from "framer-motion";
import { getDoc, doc } from "firebase/firestore";
import db from "./firebase.js";
import ImageGrid from "./ImageGrid";
import Modal from "./Modal";
import Spinner from "./Spinner";

function ProjectDetail(props) {
  let { id } = useParams();

  const [projectData, setProjectData] = useState({});
  const [selectedImg, setSelectedImg] = useState(null);
  const [spinner, setSpinner] = useState(true);
  // let isProjectData;

  const docRef = doc(db, "projects", id);

  useEffect(() => {
    getDoc(docRef)
      .then((doc) => {
        setProjectData(doc.data());
      })
      .catch((err) => {
        console.log(err);
      });
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (Object.keys(projectData).length > 0) {
      setSpinner(false);
    }
  }, [projectData]);
  return (
    <>
      {spinner ? (
        <div className="projectdetail-spinner-cont">
          <Spinner />
        </div>
      ) : (
        <>
          <div className="projectdetail-header">
            {projectData.architecture && <h2>ARCHITECTURE</h2>}
            {projectData.landscapes && <h2>LANDSCAPES</h2>}
          </div>

          <motion.div
            className="projectdetail-cont"
            initial={{ y: 10, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ bounce: 0 }}
          >
            <>
              <div className="projectinfo">
                <h1 className="projectname">{projectData.pname}</h1>
                <p className="projectlocation">{projectData.location}</p>
                <p className="projectdesc">{projectData.desc}</p>
                <a href="#gallery-header">
                  <button className="viewgallery">View Gallery</button>
                </a>
              </div>

              <div className="plan-cont">
                <img
                  onClick={() => setSelectedImg(projectData.plan)}
                  className="projectplan"
                  src={projectData.plan}
                  alt="Project Plan"
                />
                <p>Accurate project plan</p>
              </div>
            </>
          </motion.div>
          <motion.div
            id="gallery-header"
            className="gallery-header"
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            transition={{ bounce: 0 }}
          >
            <p className="gallery-heading">
              Photo <span> Gallery</span>
            </p>
            <p className="gallery-desc">Some photos of our excellent work</p>
          </motion.div>
          <motion.div
            className="projectdetails-grid-cont"
            initial={{ y: 10, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ bounce: 0 }}
          >
            {projectData.architecture && (
              <ImageGrid
                imgUrl={[...projectData.architecture, ...projectData.interior]}
                setSelectedImg={setSelectedImg}
              />
            )}
            {projectData.landscapes && (
              <ImageGrid
                imgUrl={[...projectData.landscapes]}
                setSelectedImg={setSelectedImg}
              />
            )}
          </motion.div>
          {selectedImg && (
            <Modal selectedImg={selectedImg} setSelectedImg={setSelectedImg} />
          )}
        </>
      )}
    </>
  );
}

export default ProjectDetail;
