import React from "react";
import { Link } from "react-router-dom";

function ProjectTile(props) {
  return (
    <div className="projectile-container">
      <div className="tile-img-cont">
        {props.project.architecture && (
          <img src={props.project.architecture} className="pro-img" alt="" />
        )}
        {props.project.landscapes && (
          <img src={props.project.landscapes} className="pro-img" alt="" />
        )}
        <svg
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 100 100"
          preserveAspectRatio="none"
        >
          <polygon fill="white" points="-2,100 100,35 100,110" />
        </svg>
      </div>
      <div className="project-tile-info project-tile-name">
        {props.project.pname}
      </div>
      <div className="project-tile-info project-tile-location">
        {props.project.location}
      </div>

      <Link to={`${props.project.id}`} className=" card-view-btn">
        View
      </Link>
    </div>
  );
}

export default ProjectTile;
